import { presetPrimaryColors } from "@ant-design/colors";
import { CheckCircleOutlined, DownloadOutlined } from "@ant-design/icons";
import { Button, message, Tooltip } from "antd";
import axios from "axios";
import queryClient from "components/utils/queryClient";
import React from "react";
import { SaveModuleProps } from "types";
import { useMutation } from "react-query3";

export default function SaveModuleButton({
  name,
  version,
  installed,
  mutationOptions,
}: SaveModuleProps): JSX.Element {
  const { mutateAsync: installModule, isLoading } = useMutation(
    async (module: { name: string; version: string }) =>
      await axios.put(
        `/api/v1/dashboard/marketplace/${module?.name}/${module?.version}`
      ),
    {
      onSuccess: (...args) => {
        message.success("Saved component!");
        queryClient.refetchQueries("/dashboard/marketplace");
        if (mutationOptions && mutationOptions?.onSuccess)
          mutationOptions?.onSuccess(...args);
      },
    }
  );

  return installed ? (
    <Tooltip title="Component Installed">
      <CheckCircleOutlined
        style={{ color: presetPrimaryColors["green"], padding: 8 }}
      />
    </Tooltip>
  ) : (
    <Tooltip title="Install">
      <Button
        type="text"
        icon={<DownloadOutlined />}
        onClick={() => installModule({ name, version })}
        loading={isLoading}
      />
    </Tooltip>
  );
}
