import React from "react";
import { Button, Form, Space, Radio, Tooltip } from "antd";
import { GlobalOutlined } from "@ant-design/icons";
import {
  DashboardMarketplaceModule,
  DashboardMarketplaceBrowsePage,
  DashboardMarketplaceOverview,
  CustomColumns,
} from "../../types";
import { useQuery } from "react-query3";
import Table from "components/ui/Table";
import SaveModuleButton from "./components/SaveModule";

export function Marketplace() {
  const [form] = Form.useForm();
  const [currentView, setCurrentView] = React.useState("new");
  const [page, setPage] = React.useState(1);

  const { data: modules, isLoading } = useQuery<DashboardMarketplaceOverview>(
    "/dashboard/marketplace"
  );

  const {
    isLoading: isPageLoading,
    isFetching,
    data,
    refetch,
  } = useQuery<DashboardMarketplaceBrowsePage>(
    `/dashboard/marketplace/module?page=${page}`,
    {
      enabled: currentView === "browse",
      keepPreviousData: true,
    }
  );

  const columns: CustomColumns<DashboardMarketplaceModule>[] = [
    {
      title: "Name",
      dataIndex: "title",
      key: "title",
    },
    {
      title: "Description",
      dataIndex: "description",
      key: "description",
    },
    {
      key: "version",
      title: "Version",
      dataIndex: "version",
      width: "min-content",
      align: "center",
    },
    {
      title: "Authors",
      key: "authors",
      dataIndex: "authors",
    },
    {
      title: "Downloads",
      key: "downloadCount",
      dataIndex: "downloadCount",
      width: "min-content",
      align: "center",
    },
    {
      title: "Actions",
      key: "actions",
      align: "left",
      width: "min-content",
      render: (text, data) => {
        return (
          <Space>
            <SaveModuleButton
              name={data.title}
              version={data.version}
              installed={data.installed}
              mutationOptions={{
                onSuccess: () => {
                  if (currentView === "browse") refetch();
                },
              }}
            />
            <Tooltip title="View">
              <Button
                type="text"
                icon={<GlobalOutlined />}
                onClick={(x) =>
                  window.open(
                    `https://marketplace.universaldashboard.io/Dashboard/${data.title}`
                  )
                }
              />
            </Tooltip>
          </Space>
        );
      },
    },
  ];

  return (
    <Table
      title="Marketplace"
      showSearch={currentView !== "browse"}
      showMarketplaceSearch={currentView === "browse"}
      actions={[
        <Form
          name="published_folder_editable_form"
          form={form}
          onValuesChange={({ dataToDisplay }) => {
            setCurrentView(dataToDisplay);
          }}
        >
          <Form.Item name="dataToDisplay" style={{ margin: 0 }} noStyle>
            <Radio.Group buttonStyle="solid" defaultValue={currentView} >
              <Radio.Button value="new" defaultChecked={true}>
                New
              </Radio.Button>
              <Radio.Button value="popular">Popular</Radio.Button>
              <Radio.Button value="browse">Browse</Radio.Button>
            </Radio.Group>
          </Form.Item>
        </Form>,
      ]}
      // @ts-ignore
      columns={columns}
      loading={isLoading || isPageLoading || isFetching}
      data={
        currentView === "new"
          ? modules?.newPackages
          : currentView === "popular"
          ? modules?.mostDownloadedPackages
          : data?.modules
      }
      rowKey={(record: DashboardMarketplaceModule) => record.id || "key"}
      pagination={{
        style: { display: currentView !== "browse" && "none" },
        total: data?.totalItems || 10,
        onChange: (page) => setPage(page),
      }}
    />
  );
}

export default Marketplace;
